import { createValidator, ValidationRules } from '../utils/validation.utils';
import { FormErrors } from '../errors/validation.error';
import { ServiceForm } from '../forms/service.forms';

export const validateServiceForm = (form: ServiceForm): FormErrors<ServiceForm> => {
  const errors: FormErrors<ServiceForm> = {};

  errors.name = createValidator([
    { check: (v) => ValidationRules.required(v), message: 'Service name is required' },
    { check: (v) => ValidationRules.minLength(v, 2), message: 'Service name must be at least 2 characters' },
    { check: (v) => ValidationRules.maxLength(v, 100), message: 'Service name must be less than 100 characters' }
  ])(form.name);

  errors.serviceCode = createValidator([
    { check: (v) => !v || ValidationRules.required(v), message: 'Service code is required' },
    { check: (v) => !v || ValidationRules.minLength(v, 2), message: 'Service code must be at least 2 characters' },
    { check: (v) => !v || ValidationRules.maxLength(v, 50), message: 'Service code must be less than 50 characters' },
  ])(form.serviceCode);

  errors.description = createValidator([
    { check: (v) => !v || ValidationRules.minLength(v, 5), message: 'Description must be at least 5 characters' },
    { check: (v) => !v || ValidationRules.maxLength(v, 500), message: 'Description must be less than 500 characters' }
  ])(form.description);

  errors.price = createValidator([
    { check: (v) => v >= 0, message: 'Price cannot be negative' },
    { check: (v) => ValidationRules.decimal(v), message: 'Please enter a valid price' }
  ])(form.price);

  errors.duration = createValidator([
    { check: (v) => v >= 0, message: 'Duration cannot be negative' }
  ])(form.duration);

  errors.observations = createValidator([
    { check: (v) => !v || ValidationRules.maxLength(v, 1000), message: 'Observations must be less than 1000 characters' }
  ])(form.observations);

  return errors;
};
